<!--
    This form serves to select an already deployed contract on our system.
    It also allows you to select another contract from explorer or to upload it.
-->

<template>

    <div>
        
        <v-dialog v-model="explorerDlg" :persistent="loading" transition="dialog-top-transition">
            <v-card>
                <v-card-title>Block Explorer</v-card-title>
                <v-card-subtitle class="d-flex justify-start">Load verified contract from block explorer</v-card-subtitle>
                <v-card-text>
                    <v-progress-circular v-if="loading" :size="70" :width="7" color="primary" indeterminate></v-progress-circular>
                    <div v-else>
                        <p>
                            Enter the address of a verified contract from block explorer (Etherscan, etc.).
                            Make sure the contract is verified.
                            Make sure you are connected to the same chain on which the contract is deployed.
                        </p>
                        <v-text-field ref="expAddr" label="Address" hint="Address of the deployed contract" v-model="contractAddress" :rules="addrRules" @keyup.enter="explorerSubmit" required></v-text-field>
                    </div>
                </v-card-text>
                <v-card-actions class="justify-end">
                    <v-btn color="error" text @click="explorerDlg = false" :disabled="loading">Cancel</v-btn>
                    <v-btn color="primary" text @click="explorerSubmit" :disabled="!valid" rounded>Load</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="uploadDlg" transition="dialog-top-transition">
            <v-card>
                <v-card-title>Upload ABI</v-card-title>
                <v-card-subtitle class="d-flex justify-start">Load contract from ABI</v-card-subtitle>
                <v-card-text>
                        <p>
                            Enter the address of a deployed contract.
                            Enter the contract ABI in JSON format.
                            Make sure you are connected to the same chain on which the contract is deployed.
                        </p>
                        <v-text-field ref="uplAddr" label="Address" hint="Address of the deployed contract" v-model="contractAddress" :rules="addrRules" required></v-text-field>
                        <v-textarea
                            ref="uplAbi"
                            label="ABI"
                            hint="Enter contract ABI in JSON format"
                            v-model="contractAbi"
                            :rules="abiRules"
                            rows=6
                        ></v-textarea>
                </v-card-text>
                <v-card-actions class="justify-end">
                    <v-btn color="error" text @click="uploadDlg = false" rounded>Cancel</v-btn>
                    <v-btn color="primary" text @click="uploadSubmit" :disabled="!valid" rounded>Load</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-card>
            <v-card-title>Select contract</v-card-title>

            <v-card-text class="d-flex justify-space-around custom-card-body">

                <v-select
                    v-model="selectedContract"
                    :items="contractsArray"
                    item-text="name"
                    item-value="address"
                    label="Contract"
                    single-line
                    :readonly="loading"
                    dense
                    :disabled="!userContracts"
                    @input="selectInput"
                ></v-select>
                <div class="d-flex justify-center">
                    <v-btn color="primary" @click="explorerDlg = true" class="ml-2 mt-2 flex-grow-1" rounded :disabled="!mm">
                        <v-progress-linear v-if="explorerDlg" indeterminate rounded height="7" color="warning"></v-progress-linear>
                        <span v-else>Explorer</span>
                    </v-btn>

                    <v-btn color="primary" @click="uploadDlg = true" class="ml-2 mt-2 flex-grow-1" rounded :disabled="!mm">
                        <v-progress-linear v-if="uploadDlg" indeterminate rounded height="7" color="warning"></v-progress-linear>
                        <span v-else>Upload</span>
                    </v-btn>
                </div>

            </v-card-text>
        </v-card>

    </div>

</template>

<script>

    import { mapState, mapActions } from 'vuex';

    export default {
        name: 'ContractSelect',

        data() {
            return {
                selectedContract: '',

                explorerDlg: false,
                uploadDlg: false,
                loading: false,
                valid: false,

                contractAddress: '',
                addrRules: [
                    v => !!v || 'Required',
                    v => this.w3 && this.w3.utils.isAddress(v) || 'Must be valid Ethereum address'
                ],

                contractAbi: '',
                abiRules: [
                    v => !!v || 'Required',
                    v => this.isJSON(v) || 'Must be valid JSON string'
                ]
            }
        },

        computed: {
            ...mapState({
                'userContracts': state => state.db.userContracts,
                'mm': state => state.chain.mm,
                'w3': state => state.chain.w3,
                'currentChain': state => state.chain.currentChain
            }),

            contractsArray() {
                const conts = [];

                for (let cont in this.userContracts) {
                    conts.push(this.userContracts[cont]);
                }

                return conts;
            }
        },

        watch: {
            contractAddress() {
                if (this.explorerDlg) {
                    this.$refs.expAddr.validate();

                    if (this.$refs.expAddr.valid) {
                        this.valid = true;
                    }
                } else if (this.uploadDlg) {
                    this.$refs.uplAddr.validate();
                    this.$refs.uplAbi.validate();

                    if (this.$refs.uplAddr.valid && this.$refs.uplAbi.valid) {
                        this.valid = true;
                    }
                }
            },

            contractAbi() {
                this.$refs.uplAddr.validate();
                this.$refs.uplAbi.validate();

                if (this.$refs.uplAddr.valid && this.$refs.uplAbi.valid) {
                    this.valid = true;
                }
            },

            explorerDlg(val) {
                if (!val) {
                    this.contractAddress = '';
                    this.valid = false;
                } else {
                    this.$nextTick( () => {
                        setTimeout( () => {
                            this.$refs.expAddr.focus();
                        })
                    })
                }
            },

            uploadDlg(val) {
                if (!val) {
                    this.contractAddress = '';
                    this.contractAbi = '';
                    this.valid = false;
                } else {
                    this.$nextTick( () => {
                        setTimeout( () => {
                            this.$refs.uplAddr.focus();
                        })
                    })
                }
            },

            userContracts: {
                handler: function(val) {
                    if (val && this.$route.query.address) {
                        for (let i in val) {
                            const cnt = val[i];

                            if (cnt.address == this.$route.query.address) {
                                this.selectedContract = cnt;
                            }
                        }
                    }
                },
                immediate: true
            }
        },

        methods: {
            ...mapActions({
                'addLoadedUserContract': 'db/addLoadedUserContract',
            }),

            explorerSubmit() {
                this.contractAddress = this.contractAddress.toLowerCase();
                this.selectedContract = this.contractAddress;
                this.$emit('contractSelected', this.selectedContract);
            },

            async uploadSubmit() {
                this.contractAddress = this.contractAddress.toLowerCase();
                await this.addLoadedUserContract({
                    address: this.contractAddress,
                    abi: this.contractAbi,
                    chain: this.currentChain
                });

                this.selectedContract = this.contractAddress;
                this.$emit('contractSelected', this.selectedContract);
            },

            isJSON(str) {
                try {
                    JSON.parse(str);
                } catch (e) {
                    return false;
                }
                return true;
            },

            selectInput() {
                this.$emit('contractSelected', this.selectedContract);
            }
        }

    }

</script>

<style scoped>

    .v-select {
        max-width: 250px;
        flex-grow: 0;
    }

    .custom-card-body {
        flex-direction: row;
    }

    @media only screen and (max-width: 900px) {
        .v-select {
            max-width: 100%;
            flex-grow: 1;
        }

        .custom-card-body {
            flex-direction: column;
        }
    }

</style>
