<!--
    This component is used to manage already deployed contracts
    url: http://localhost:8080/#/manager

    components:
        TransactionForm,
        ContractSelect,
        Converter
-->

<template>

    <div>

        <progress-dlg v-if="progressDlg" :show="progressDlg" persistent>
            <v-card>
                <v-card-title></v-card-title>
                <v-card-subtitle>
                    <p>Verifying contract...</p>
                    <p>This may take a few minutes.</p>
                    <p><strong>Do not close or refresh this page!</strong></p>
                </v-card-subtitle>
                <v-card-text>
                    <v-progress-circular :size="70" :width="7" color="primary" indeterminate></v-progress-circular>
                </v-card-text>
            </v-card>
        </progress-dlg>

        <v-container>
            <v-row class="justify-center">
                <v-col md="5" sm="8" xs="12">
                    <contract-select @contractSelected="fetchAbi" ref="cSelect"/>
                </v-col>
            </v-row>
            <v-row class="justify-center">
                <transition name="cards" mode="out-in">
                    <v-col v-if="abi" lg="7" md="8" sm="12">
                        <transaction-form
                            :abi="abi"
                            :address="address"
                            :explorerURL="explorerURL"
                            :response="response"
                            :token="token"
                            :verify="verify"
                            @formSubmitted="submitTransaction"
                            @addToMM="addToMetaMask"
                            @verifyOnBE="verifyOnBlockExplorer"
                            ref="dForm"
                            class="ml-1 mb-1" />
                    </v-col>
                </transition>
                <transition name="cards" mode="out-in">
                    <v-col v-if="abi" md="4" sm="6" xs="12">
                        <converter />
                    </v-col>
                </transition>
            </v-row>
        </v-container>
    </div>

</template>

<script>

    import ContractSelect from '@/components/manager/ContractSelect';
    import { doc, getDoc } from 'firebase/firestore';
    import { mapState, mapActions } from 'vuex';
    import { endpoints } from '@/store/constants';
    const GenericDlg = () => import('@/components/GenericDlg');
    const TransactionForm = () => import('@/components/manager/TransactionForm');
    const Converter = () => import('@/components/manager/Converter');

    export default {
        name: 'Manager',

        components: {
            'transaction-form': TransactionForm,
            'contract-select': ContractSelect,
            'converter': Converter,
            'progress-dlg': GenericDlg
        },


        data() {
            return {
                abi: null,
                address: null,
                abis: {},
                chains: [],
                correctChain: false,
                liveContract: null,
                response: {},
                token: false,
                verify: false,
                progressDlg: false
            }
        },

        computed: {
            ...mapState({
                'w3': state => state.chain.w3,
                'wallet': state => state.chain.wallet,
                'currentChain': state => state.chain.currentChain,
                'userContracts': state => state.db.userContracts,
                'db': state => state.db.db
            }),

            explorerURL() {
                const chain = this.chains.filter( c => c.id == this.currentChain )[0];

                if (chain) {
                    return chain.blockExplorerUrls[0];
                } else {
                    return '';
                }
            }
        },

        mounted() {
            this.chains = endpoints;

            if (this.$route.query.address) {
                this.fetchAbi(this.$route.query.address);
            }
        },

        methods: {
            ...mapActions({
                'displayAlert': 'alert/displayAlert',
                'addLoadedUserContract': 'db/addLoadedUserContract',
                'addUserContract': 'db/addUserContract'
            }),

            submitTransaction(trans, args) {
                this.response = {};
                const ars = [];
                let value = 0;

                let v = args.filter( a => a.name == 'value' )[0];
                if (v) {
                    value = this.w3.utils.toWei(v.value, 'ether');
                }

                trans.inputs.forEach( input => {
                    let val = args.filter( a => a.name == input.name )[0].value;
                    ars.push(val);
                });

                switch (trans.stateMutability) {
                    case 'view':
                    case 'pure':
                        this.liveContract.methods[trans.name](...ars).call({ from: this.wallet })
                            .then( msg => {
                                this.response = { msg };
                                this.$refs.dForm.transRunning = false;
                            })
                            .catch( err => {
                                this.response = this.errorToJSON(err.message);
                                this.$refs.dForm.transRunning = false;
                            });
                        break;
                    case 'nonpayable':
                    case 'payable':
                        this.liveContract.methods[trans.name](...ars).estimateGas({ from: this.wallet, value: value })
                            .then( gas => {
                                this.liveContract.methods[trans.name](...ars).send({ gas: gas, from: this.wallet, value: this.w3.utils.toHex(value) })
                                    .on('receipt', rcpt => {
                                        this.response = rcpt;
                                        this.$refs.dForm.transRunning = false;
                                    })
                                    .on('error', (err, ) => {
                                        this.response = this.errorToJSON(err.message);
                                        this.$refs.dForm.transRunning = false;
                                    });
                            }).catch( err => {
                                this.response = this.errorToJSON(err.message);
                                this.$refs.dForm.transRunning = false;
                            });
                }
            },

            errorToJSON(error) {
                try {
                    let i = error.indexOf('{');
                    let msg = '{"' + error.slice(0, i-1) + '":' + error.slice(i) + '}';
                    
                    return JSON.parse(msg);
                } catch {
                    return { msg: error };
                }
            },

            async fetchAbi(address) {
                while (this.userContracts === null) { await this.sleep(100); }
                const contract = this.userContracts[address];
                this.address = address;

                if (this.userContracts && contract) {
                    if (contract.chain !== this.currentChain) {
                        await this.switchChain(contract.chain);
                    } else {
                        this.correctChain = true;
                    }

                    if (!this.correctChain) {
                        this.$refs.cSelect.loading = false;
                        return;
                    }

                    try {
                        if (this.abis[address]) {
                            this.abi = this.abis[address];

                            this.connectContract(this.abi, this.address);

                            this.$refs.cSelect.loading = false;
                            this.$refs.cSelect.explorerDlg = false;
                            this.$refs.cSelect.uploadDlg = false;
                            return;
                        }

                        const snap = await getDoc(doc(this.db, 'abi', contract.abi))

                        if (snap.exists()) {
                            this.abi = snap.data().abi;
                            this.abis[address] = this.abi;

                            this.connectContract(this.abi, this.address);

                            this.$refs.cSelect.loading = false;
                            this.$refs.cSelect.explorerDlg = false;
                            this.$refs.cSelect.uploadDlg = false;
                        } else {
                            this.fetchAbiFromExplorer(address);
                        }
                    } catch(err) {
                        this.displayAlert({ message: err.message, type: 'error' });
                    }
                } else {
                    this.fetchAbiFromExplorer(address);
                }
            },

            async fetchAbiFromExplorer(address) {
                const chain = this.chains.filter( el => el.id === this.currentChain )[0];
                while (!chain) { await this.sleep(100); }
                fetch(`${chain.blockExplorerAPIUrls[0]}?module=contract&action=getabi&address=${address}&apikey=${chain.blockExplorerAPIKey}`)
                    .then( obj => obj.json() ).then( res => {
                        if (res.status == 1) {
                            this.abi = JSON.parse(res.result);
                            this.abis[address] = this.abi;

                            this.addLoadedUserContract({ address: address, abi: this.abi, chain: this.currentChain });

                            this.connectContract(this.abi, this.address);

                            this.correctChain = true;
                            this.$refs.cSelect.loading = false;
                            this.$refs.cSelect.explorerDlg = false;
                            this.$refs.cSelect.uploadDlg = false;
                        } else {
                            this.$refs.cSelect.loading = false;
                            this.$refs.cSelect.explorerDlg = false;
                            this.$refs.cSelect.uploadDlg = false;

                            this.displayAlert({ message: 'Please make sure that requested contract is on the currently selected blockchain and that the contract is verified on block explorer.', type: 'error', dismissible: true });
                        }

                    }).catch( err => {
                        this.$refs.cSelect.loading = false;
                        this.$refs.cSelect.explorerDlg = false;
                        this.$refs.cSelect.uploadDlg = false;

                        this.displayAlert({ message: err.message, type: 'error' });
                    });
            },

            async connectContract(abi, address) {
                this.liveContract = new this.w3.eth.Contract(abi, address);

                try {
                    const s = await this.liveContract.methods.symbol().call();
                    this.token = true;
                } catch (e) {
                    this.token = false;
                }

                this.verify = false;
                const ucnt = this.userContracts[address];
                if (ucnt.constructorArguements && !ucnt.verified) {
                    this.verify = true;
                }
            },

            async switchChain(chainId) {
                try {
                    await this.w3.currentProvider.request({
                        method: 'wallet_switchEthereumChain',
                        params: [{ chainId: '0x' + chainId.toString(16) }]
                    });

                    this.correctChain = true;
                } catch (err) {
                    if (err.code === 4001) {
                        this.correctChain = false;
                        this.displayAlert({ message: 'Please select the blockchain on which the requested contract is deployed.', type: 'error' });
                    }

                    if (err.code === 4902) {
                        try {
                            const chain = this.chains.filter( el => el.id === chainId )[0];

                            await this.w3.currentProvider.request({
                                method: 'wallet_addEthereumChain',
                                params: [{
                                    chainId: '0x' + chain.id.toString(16),
                                    chainName: chain.name,
                                    rpcUrls: chain.rpcUrls,
                                    nativeCurrency: {
                                        name: chain.nativeCurrency.name,
                                        symbol: chain.nativeCurrency.symbol,
                                        decimals: chain.nativeCurrency.decimals,
                                    },
                                    blockExplorerUrls: chain.blockExplorerUrls
                                }]
                            });

                            this.correctChain = true;
                        } catch (err) {
                            if (err.code === 4001) {
                                this.correctChain = false;
                                this.displayAlert({ message: 'Please select the blockchain on which the requested contract is deployed.', type: 'error' });
                            }
                        }
                    }
                }

                await this.sleep(1000);
            },

            async addToMetaMask() {
                try {
                    const s = await this.liveContract.methods.symbol().call();
                    const d = await this.liveContract.methods.decimals().call();

                    this.w3.currentProvider.request({
                        method: 'wallet_watchAsset',
                        params: {
                            type: 'ERC20',
                            options: {
                                address: this.address,
                                symbol: s,
                                decimals: d
                            }
                        }
                    }).catch( err => this.displayAlert({ message: err.message, type: 'error' }) );
                } catch (err) {
                    this.displayAlert({ message: err.message, type: 'error' })
                }
            },

            async verifyOnBlockExplorer() {
                this.progressDlg = true;

                const chain = this.chains.filter( el => el.id === this.currentChain )[0];
                while (!chain) { await this.sleep(100); }
                this.chainExplorerURL = chain.blockExplorerUrls[0];

                const contract = this.userContracts[this.address];
                contract['wallet'] = this.wallet;

                const code = await getDoc(doc(this.db, 'code', contract.abi));

                if (!code.exists()) {
                    this.displayAlert({ message: 'Error fetching compiled contract', type: 'error' });
                }

                fetch(chain.blockExplorerAPIUrls[0], {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
                    },
                    body: new URLSearchParams({
                        apikey: chain.blockExplorerAPIKey,
                        contractaddress: this.address,
                        contractname: contract.abi,
                        optimizationUsed: 1,
                        runs: 200,
                        licenseType: 3,
                        module: 'contract',
                        action: 'verifysourcecode',
                        codeformat: 'solidity-single-file',
                        sourceCode: code.data().source,
                        compilerversion: 'v' + code.data().compilerVersion,
                        evmversion: code.data().evmVersion,
                        constructorArguements: contract.constructorArguements.slice(2)
                    })
                }).then( obj => obj.json() ).then( async res => {
                    if (res.status == '1') {

                        const url = new URL(chain.blockExplorerAPIUrls[0]);
                        url.search = new URLSearchParams({
                            apikey: chain.blockExplorerAPIKey,
                            guid: res.result,
                            module: 'contract',
                            action: 'checkverifystatus'
                        });

                        let done = false;

                        while (!done) {
                            try {
                                const objV = await fetch(url);
                                const resV = await objV.json();

                                if (resV.status == '1') {
                                    this.progressDlg = false;
                                    done = true;

                                    contract.verified = true;
                                    this.verify = false;
                                    this.addUserContract(contract);
                                    this.displayAlert({ message: 'Contract verified.', type: 'success' });
                                } else {
                                    if (resV.result === 'Pending in queue') {
                                        await this.sleep(30000);
                                    } else if (resV.result === 'Already Verified') {
                                        this.progressDlg = false;
                                        done = true;

                                        contract.verified = true;
                                        this.verify = false;
                                        this.addUserContract(contract);
                                        this.displayAlert({ message: 'Contract verified.', type: 'success' });
                                    } else {
                                        this.progressDlg = false;
                                        done = true;

                                        this.displayAlert({ message: 'Contract not verified. Reason: ' + resV.result, type: 'error' });
                                    }
                                }
                            } catch(err) {
                                this.progressDlg = false;
                                done = true;

                                this.displayAlert({ message: err.message, type: 'error' });
                            }
                        }

                    } else {
                        this.progressDlg = false;
                        this.displayAlert({ message: res.result, type: 'error' });
                    }
                }).catch( err => {
                    this.progressDlg = false;
                    this.displayAlert({ message: err.message, type: 'error' });
                });
            },

            sleep(ms) {
                return new Promise(resolve => setTimeout(resolve, ms));
            }
        }

    }

</script>

<style scoped>

    .col {
        flex-basis: 100%;
    }

    .cards-enter-active,
    .cards-leave-active {
        transition: all .5s ease;
    }

    .cards-enter,
    .cards-leave-to {
        opacity: 0;
        transform: translateY(-30px);
    }

</style>
